<template>
  <ec-modal
    :visible="visible"
    :title="title"
    size="large"
    class="ec-draw-form"
    @cancel="onclose"
    :is-confirm="true"
  >
    <ec-form :model="formData" :rules="rules" ref="modelRef">
      <a-row type="flex" :gutter="12">
        <a-col :span="12">
          <a-form-model-item label="申请单位" prop="applyOrgCodes" ref="orgCodeList">
            <org-picker v-model="formData.applyOrgCodes" placeholder="请选择" />
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item prop="storeCode" label="仓库">
            <a-select allowClear v-model="formData.storeCode" placeholder="请选择">
              <a-select-option
                v-for="item in storeList"
                :key="item.storeCode"
                :value="item.storeCode"
                >{{ item.storeName }}</a-select-option
              >
            </a-select>
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item ref="leafTypeCode" prop="leafTypeCode" label="烟叶类型">
            <ec-select
              placeholder="请选择"
              v-model="formData.leafTypeCode"
              @select="leafTypeChange"
            >
              <a-select-option v-for="leafType in leafTypeList" :key="leafType.leafTypeCode">
                {{ leafType.leafTypeName }}
              </a-select-option>
            </ec-select>
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item prop="leafFormCode" label="烟叶类型">
            <ec-select placeholder="请选择" v-model="formData.leafFormCode" @select="handleSpec">
              <a-select-option v-for="leafType in leafFormList" :key="leafType.leafFormCode">
                {{ leafType.leafFormName }}
              </a-select-option>
            </ec-select>
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item ref="packType" prop="materialCode" label="物料">
            <ec-select placeholder="请选择" v-model="formData.materialCode">
              <a-select-option v-for="packType in packTypeList" :key="packType.enumKey">
                {{ packType.name }}
              </a-select-option>
            </ec-select>
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item ref="packType" prop="packType" label="包装类型">
            <ec-select placeholder="请选择" v-model="formData.packType">
              <a-select-option v-for="packType in packTypeList" :key="packType.enumKey">
                {{ packType.name }}
              </a-select-option>
            </ec-select>
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item ref="spec" prop="spec" label="规格">
            <ec-select placeholder="请选择" @select="applyNumChange" v-model="formData.spec">
              <a-select-option
                v-for="rtSpec in specList"
                :key="rtSpec.enumKey"
                :value="rtSpec.enumKey"
              >
                {{ rtSpec.name }}
              </a-select-option>
            </ec-select>

            <!-- <a-select v-model="formData.spec" placeholder="请选择" @select="handleApplyNum">
              <a-select-option
                v-for="(item, index) in leafTypeList"
                :key="index"
                :value="item.leafTypeCode"
                >{{ item.leafTypeName }}
              </a-select-option>
            </a-select> -->
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item label="申请码包数量" prop="applyNum">
            <a-input-number
              placeholder="请输入"
              @change="applyNumChange"
              :min="0"
              v-model="formData.applyNum"
            />
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item label="申请量（KG）" prop="applyWeight">
            <a-input-number :disabled="true" v-model="formData.applyWeight" />
          </a-form-model-item>
        </a-col>
      </a-row>
    </ec-form>
    <template v-slot:footer>
      <a-button type="primary" @click="onSave" :loading="saving">保存</a-button>
      <a-button @click="onclose">取消</a-button>
    </template>
  </ec-modal>
</template>

<script>
import { OrgPicker } from '@euler-material/ecm-ui';

export default {
  name: 'batchManageForm',
  components: {
    OrgPicker,
  },
  props: {
    showEdit: {
      type: Boolean,
      default: false,
    },
    storeList: {
      type: [Array, String],
      default: () => [],
    },
  },
  computed: {
    _api() {
      return this.$apis.pcm.penatly.apply;
    },
  },
  data() {
    return {
      leafFormList: [],
      packTypeList: [],
      storesList: [],
      visible: false,
      formData: {
        storeCode: '',
      },
      isEditor: false,
      saving: false,
      isDisableYear: true,
      busiYear: '',
      title: '',
      leafTypeList: [],
      orgCodeList: [],
      specList: [],
      applyOrgCode: '',
      applyOrgName: '',
      rules: {
        materialCode: [{ message: '请选择物料', required: true, trigger: 'change' }],
        applyOrgCodes: [{ message: '请选择申请单位', required: true, trigger: 'change' }],
        leafTypeCode: { required: true, message: '请选择烟叶类型', trigger: 'change' },
        packType: {
          required: true,
          message: '请选择包装类型',
          trigger: 'change',
        },
        spec: {
          required: true,
          message: '请选择规格',
          trigger: 'change',
        },
        // applyWeight: {
        //   required: true,
        //   message: '请输入申请量',
        //   trigger: 'blur',
        // },
        applyNum: {
          required: true,
          message: '请选择规格和输入申请码包量',
          trigger: 'blur',
        },
      },
    };
  },
  methods: {
    applyNumChange() {
      if (!this.formData.applyNum || !this.formData.spec) {
        this.formData.applyWeight = 0;
        return;
      }
      this.formData.applyWeight = this.formData.applyNum * Number(this.formData.spec);
    },
    async getLeafTypeList() {
      if (!this.leafTypeList?.length) {
        const res = await this.$apis.bpm.executionEnter.queryLeafType({});
        const rows = res?.rows || [];
        if (rows && rows.length > 0) {
          rows.unshift({ leafTypeName: '全部', leafTypeCode: '' });
        }
        this.leafTypeList = rows;
        this.leafTypeList.some((item) => {
          if (item.leafTypeName === '烤烟') {
            this.formData.leafTypeCode = item.leafTypeCode;
            return true;
          } else {
            return false;
          }
        });
      }
    },
    async leafTypeChange(data) {
      await this._api.spinnerLeafForm({ leafTypeCodes: [data] }).then((info) => {
        this.leafFormList = info?.rows;
        this.leafFormList.some((item) => {
          if (item.leafFormName === '片烟') {
            // this.leafTypeCd = item.leafTypeCode;
            this.$set(this.formData, 'leafFormCode', item.leafFormCode);
            return true;
          } else {
            return false;
          }
        });
      });
    },
    show(data) {
      this.visible = true;
      const type = data.type || '';
      const orgCodeList = data?.orgCodeList || [];
      this.orgCodeList = orgCodeList;
      const orgInfo = data.orgInfo || {};
      const selectedRow = data?.selectedRow || {};

      if (type === 'editor') {
        this.isEditor = true;
        this.title = '编辑';
        this.formData = selectedRow;
        this.formData.tranYear = data.tranYear;
        this.formData.applyOrgCodes = [selectedRow.applyOrgCode];
      } else {
        this.isEditor = false;
        this.title = '新增';
        this.formData.tranYear = data.tranYear;
        this.formData.applyOrgCode = data.orgCode;
        this.formData.applyType = 'INVENTORY_CP'; // 写死
        this.$set(this.formData, 'applyOrgCodes', [data.orgInfo.provUniqueCode]);
      }

      this.busiYear = data.busiYear;
      this.formData.applyOrgName = orgInfo.orgName;
      console.log(this.formData);
      const enumMap = data?.enumMap || {};
      const applyStateList = enumMap?.PC_APPLY_STATE || [];
      this.applyStateList = applyStateList;

      this.leafTypeList = data?.oldLeafTypeList || [];

      const packTypeList = enumMap?.PC_PACK_TYPE || [];
      this.packTypeList = packTypeList;
      this.specList = enumMap?.PC_RT_SPEC || [];
      const pack = packTypeList.find((item) => item.name === '烟箱');
      this.formData.packType = pack ? pack.enumKey : '';
      console.log(this.specList);
    },
    // handleApplyOrgName(option) {
    //   this.orgName = option?.selectOption[0].title;
    // },
    handleApplyNum() {
      const { spec } = this.formData;
      const specValue = Number(spec) || 0;
      const { applyWeight } = this.formData;
      const applyWeightValue = Number(applyWeight) || 0;

      const applyNum = specValue === 0 ? 0 : (applyWeightValue / specValue).toFixed(2);
      this.formData.applyNum = Math.ceil(applyNum) || 0;
    },
    async handleSpec() {
      const { orgCodeList } = this;

      const applyOrgCode = orgCodeList.length > 0 ? orgCodeList[0] : '';
      const data = {
        applyOrgCd: applyOrgCode,
        leafTypeCd: this.formData.leafTypeCode,
        packType: this.formData.packType,
        // leafFormCd: '101',
        specState: '1',
      };
      console.log(`包装规格参数2：${JSON.stringify(data)}`);
      const specList = await this.$apis.pcm.buy.packApply.querySpec(data);
      this.specList = specList?.rows || [];
    },
    // 保存
    onSave() {
      this.$refs.modelRef.validate(async (valid) => {
        // debugger;
        if (!valid) return;
        this.saving = true;
        const data = this.formData;
        const { leafTypeCode, leafFormCode } = data;
        const leafTypeObj = this.leafTypeList.find((item) => item.leafTypeCode === leafTypeCode);
        const leafFormObj = this.leafFormList.find((item) => item.leafFormCode === leafFormCode);
        data.leafTypeName = leafTypeObj?.leafTypeName;
        data.leafFormName = leafFormObj?.leafFormName;
        data.applyOrgCode = data.applyOrgCodes.length ? data.applyOrgCodes.join('') : '';
        console.log(data);
        if (this.isEditor) {
          this._api
            .updatePcTCpApply(data)
            .then((res) => {
              console.log(res);
              this.onclose();
              this.$emit('onQuery');
              this.$message.success('操作成功');
            })
            .catch((err) => {
              console.log(err);
              this.saving = false;
              // this.$message.error('操作失败');
            });
        } else {
          this._api
            .createPcTCpApply(data)
            .then((res) => {
              console.log(res);
              this.onclose();
              this.$emit('onQuery');
              this.$message.success('操作成功');
            })
            .catch((err) => {
              console.log(err);
              // this.$message.error('操作失败');
              this.saving = false;
            });
        }
      });
    },
    // 取消
    onclose() {
      this.saving = false;
      this.formData = {};
      this.$emit('update:showEdit', false);
      this.visible = false;
      this.$refs.modelRef.resetFields();
    },
  },
  async created() {
    await this.getLeafTypeList();
    await this.leafTypeChange(this.formData.leafTypeCode);
    console.log('created');
  },
};
</script>
