<template>
  <ec-container height="100%">
    <ec-container-header>
      <ec-panel-item>
        <ec-panel-item-left>
          <ec-header title="历史库存陈烟码包申请" icon="onetomany" color-code="008" />
        </ec-panel-item-left>
      </ec-panel-item>
    </ec-container-header>
    <ec-container-body :auto="false">
      <ec-v-layout>
        <ec-v-item>
          <ec-panel>
            <ec-panel-body>
              <a-descriptions bordered :column="4">
                <a-descriptions-item label="调拨年度">
                  {{ queryItems.tranYear }}
                </a-descriptions-item>
                <a-descriptions-item label="烟叶类型">
                  {{
                    getLabelName(
                      queryItems.leafTypeCode,
                      this.leafTypeList,
                      'leafTypeCode',
                      'leafTypeName',
                    ) || '全部'
                  }}</a-descriptions-item
                >
                <a-descriptions-item label="计划量">
                  {{ totalData.planTotalWeight }}
                </a-descriptions-item>

                <a-descriptions-item label="剩余计划量">
                  {{ totalData.leftWeight }}
                </a-descriptions-item>
                <a-descriptions-item label="申请码包数量">
                  {{ totalData.applyNum }}
                </a-descriptions-item>
                <a-descriptions-item label="申请量">
                  {{ totalData.applyWeight }}
                </a-descriptions-item>
                <a-descriptions-item label=""> </a-descriptions-item>
                <a-descriptions-item label=""> </a-descriptions-item>
              </a-descriptions>
            </ec-panel-body>
          </ec-panel>
          <ec-panel>
            <ec-panel-toolbar>
              <ec-query-panel
                :defaultFold="true"
                @query="$refs.table.pageAction(1)"
                @reset="onReset"
              >
                <ec-query-item label="调拨年度">
                  <a-input-number
                    placeholder="请输入"
                    v-model="queryItems.tranYear"
                  ></a-input-number>
                </ec-query-item>
                <ec-query-item label="申请单位">
                  <org-picker
                    :disabled="true"
                    v-model="queryItems.applyOrgCodes"
                    placeholder="请选择"
                  />
                </ec-query-item>

                <ec-query-item label="烟叶类型">
                  <ec-select
                    showSearch
                    allowClear
                    @select="leafTypeChange"
                    placeholder="请选择"
                    v-model="queryItems.leafTypeCode"
                  >
                    <a-select-option
                      v-for="leafTypeEk in leafTypeList"
                      :key="leafTypeEk.leafTypeCode"
                    >
                      {{ leafTypeEk.leafTypeName }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="烟叶形态">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="queryItems.leafFormCode"
                  >
                    <a-select-option
                      v-for="item in leafFormList"
                      :key="item.leafFormCode"
                      :value="item.leafFormCode"
                      >{{ item.leafFormName }}</a-select-option
                    >
                  </ec-select>
                </ec-query-item>

                <template #inner>
                  <ec-query-item label="仓库">
                    <a-select allowClear v-model="queryItems.storeCode" placeholder="请选择">
                      <a-select-option
                        v-for="item in storesList"
                        :key="item.storeCode"
                        :value="item.storeCode"
                        >{{ item.storeName }}</a-select-option
                      >
                    </a-select>
                    <!-- <org-picker v-model="orgCodeList" placeholder="请选择" /> -->
                  </ec-query-item>
                  <ec-query-item label="物料">
                    <ec-select placeholder="请选择" v-model="queryItems.materialCode">
                      <a-select-option v-for="rtSpec in specList" :key="rtSpec.spec">
                        {{ rtSpec.spec }}
                      </a-select-option>
                    </ec-select>
                  </ec-query-item>

                  <ec-query-item label="状态">
                    <ec-select placeholder="请选择" v-model="queryItems.applyState">
                      <a-select-option
                        v-for="applyState in applyStateList"
                        :key="applyState.enumKey"
                      >
                        {{ applyState.name }}
                      </a-select-option>
                    </ec-select>
                  </ec-query-item>
                </template>
              </ec-query-panel>
              <ec-divider />
            </ec-panel-toolbar>

            <ec-panel-body height="100%">
              <ec-v-layout>
                <!-- 表格工具栏 -->
                <ec-v-item>
                  <ec-panel-item>
                    <ec-panel-item-left>
                      <a-button type="primary" @click="onAdd">新增</a-button>
                      <a-button @click="onDelete">删除</a-button>
                      <a-button @click="onConfirm">确认</a-button>
                      <!-- <a-button @click="onExport">导出</a-button> -->
                    </ec-panel-item-left>
                    <ec-panel-item-right>
                      <a-space :size="15">
                        <span
                          >重量单位：<ec-select
                            showSearch
                            allowClear
                            placeholder="请选择"
                            v-model="weightUnit"
                            style="width: 150px"
                          >
                            <a-select-option
                              v-for="weightUnit in weightUnitList"
                              :key="weightUnit.weightUnitCd"
                            >
                              {{ weightUnit.weightUnitName }}
                            </a-select-option>
                          </ec-select></span
                        >
                      </a-space>
                    </ec-panel-item-right>
                  </ec-panel-item>
                </ec-v-item>
                <ec-v-item> </ec-v-item>
                <!-- 列表 -->
                <ec-v-item>
                  <ec-table
                    ref="table"
                    border
                    @page-action="handlePagination"
                    :pagination="!Object.keys(orgInfo).length ? {} : pagination"
                    :data="tableData.rows"
                    @checkbox-all="selectAllEvent"
                    @checkbox-change="selectRowChange"
                    :loading="loading"
                    highlight-current-row
                  >
                    <ec-table-col type="checkbox" width="50" align="center"></ec-table-col>
                    <ec-table-col title="序号" type="seq" width="70" align="center" />
                    <ec-table-col field="applyDate" title="申请日期" align="center" width="200" />
                    <ec-table-col
                      field="applyOrgName"
                      title="申请单位"
                      align="left"
                      min-width="150"
                    />
                    <ec-table-col
                      field="leafTypeName"
                      title="烟叶类型"
                      align="left"
                      min-width="150"
                    />
                    <ec-table-col field="packType" title="包装类型" align="left" min-width="150">
                      <template #default="{ row }">
                        <span>
                          {{
                            getLabelName(row.packType, enumMap['PC_PACK_TYPE'], 'enumKey', 'name')
                          }}</span
                        >
                      </template>

                      <!-- <a-select-option v-for="packType in packTypeList" :key="packType.enumKey">
                      {{ packType.name }}
                    </a-select-option> -->
                    </ec-table-col>
                    <ec-table-col
                      field="spec"
                      title="规格（KG/件）"
                      align="right"
                      min-width="150"
                    />
                    <ec-table-col
                      field="applyNum"
                      title="申请码包数量"
                      align="right"
                      min-width="150"
                    />

                    <ec-table-col
                      field="applyWeight"
                      title="申请量"
                      align="right"
                      min-width="150"
                    />
                    <ec-table-col field="applyState" title="状态" align="center" width="150">
                      <template #default="{ row }">
                        <div v-if="row.applyState === '1'">
                          <span>已确认</span>
                        </div>
                        <div v-else>
                          <span>未确认</span>
                        </div>
                      </template>
                    </ec-table-col>

                    <ec-table-col title="操作" align="center" width="150" fixed="right">
                      <template #default="{ row }">
                        <a-button type="link" @click="onEdit(row)">编辑</a-button>
                      </template>
                    </ec-table-col>
                  </ec-table>
                </ec-v-item>
              </ec-v-layout>
            </ec-panel-body>
          </ec-panel>
        </ec-v-item>
      </ec-v-layout>

      <edit
        v-if="showEdit"
        :showEdit.sync="showEdit"
        @onQuery="onQuery"
        :storeList="curOrgStores"
        ref="BuyApplyEdit"
      ></edit>
    </ec-container-body>
  </ec-container>
</template>

<script>
import edit from './Edit.vue';
import { OrgPicker } from '@euler-material/ecm-ui';
import _ from 'lodash';

export default {
  name: 'inventoryApply',
  components: {
    edit,
    OrgPicker,
  },
  data() {
    return {
      showEdit: false,
      storesList: [],
      curOrgStores: [],
      leafFormList: [],
      objectData: {},
      busiYear: '',
      orgInfo: {},
      queryItems: {
        leafFormCode: '',
        storeCode: '',
        applyOrgCodes: [],
        leafTypeCode: '',
        packType: '',
        spec: '',
        applyState: '',
      },
      defaultPackType: '',
      weightUnit: 1,
      areaUnit: 10000,
      nowYear: '',
      loading: false,
      leafTypeCd: '',
      leafTypeList: [],
      oldLeafTypeList: [],
      oldDataRow: [], // 拷贝的旧数据，单位切换的时候要在旧的数据上转换
      oldTotalData: [], // 拷贝的旧上级数据，单位切换的时候要在旧的数据上转换
      totalData: {}, // 合计的数据
      isNullData: [],
      leafTypeName: '',
      beforeLeafTypeName: '',

      weightUnitList: [
        { weightUnitCd: 1, weightUnitName: '公斤', isSelected: false },
        { weightUnitCd: 50, weightUnitName: '担', isSelected: false },
        { weightUnitCd: 500000, weightUnitName: '万担', isSelected: false },
      ],

      applyStateList: [],
      packTypeList: [],
      specList: [],
      orgCodeList: [],
      enumMap: {},
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: {
        // 数据表格
        // totalRows: 0,
        rows: [],
      },
    };
  },
  computed: {
    _api() {
      return this.$apis.pcm.penatly.apply;
    },
  },
  async created() {
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM'; // 获取当前登入信息
    const enumTypeCodes = ['PC_PACK_TYPE', 'PC_APPLY_STATE', 'PC_RT_SPEC'];
    const data = {
      enumTypeCodes,
    };
    const res = await this.$apis.enum.index.getEnumType(data);
    console.log(res);
    const enumMap = res?.enumMap || {};
    this.enumMap = _.cloneDeep(res.enumMap || {});
    const applyStateList = enumMap?.PC_APPLY_STATE || [];
    if (applyStateList && applyStateList.length > 0) {
      applyStateList.unshift({ enumKey: '', enumTypeCode: 'PC_APPLY_STATE', name: '全部' });
      this.queryItems.applyState = applyStateList[0].enumKey;
    }
    this.applyStateList = applyStateList;
    this.$set(this.queryItems, 'tranYear', new Date().getFullYear());
    await this.getCurrentUserOrg();
    await this.handleGetLeafTypeList();
    await this.leafTypeChange(this.queryItems.leafTypeCode);
    await this.onQuery();
  },
  methods: {
    getLabelName(code, array = [], key, labelName) {
      if (!code || !array.length) return '';
      const obj = array.find((item) => item[key] === code);
      return obj[labelName] || '';
    },
    // 获取当期用户信息
    getCurrentUserOrg() {
      this._api.getCurrentUserOrg({ dataState: 1 }).then(async (data) => {
        this.orgInfo = data || {};
        this.queryItems.applyOrgCodes = data ? [data.orgUniqueCode] : undefined;
        console.log(this.queryItems.applyOrgCodes);
        this.queryItems.applyOrgCode = this.queryItems.applyOrgCodes.length
          ? this.queryItems.applyOrgCodes.join('')
          : '';
        await this.getCmTStoreByOrgCode(this.queryItems.applyOrgCode);

        // this.getCmTStoreByOrgCode(this.queryItems.applyOrgCode);
        // this.queryItems.orgName = data.orgName;
      });
    },
    // 仓库
    async getCmTStoreByOrgCode(orgCode = undefined) {
      await this._api
        .getCmTStoreByOrgCode({ dataState: 1, orgCode })
        .then((data) => {
          this.storesList = data.rows || [];
          this.curOrgStores = _.cloneDeep(data.rows);
          this.curOrgStores.unshift({
            storeName: '全部',
            storeCode: '',
          });
        })
        .finally(() => {
          this.storesList.unshift({
            storeName: '全部',
            storeCode: '',
          });
          this.queryItems.storeCode = '';
        });
    },
    async leafTypeChange(data) {
      console.log(data);
      await this._api.spinnerLeafForm({ leafTypeCodes: [data] }).then((info) => {
        this.leafFormList = info?.rows;
        // 脏数据，临时删掉
        this.leafFormList.splice(0, 1);
        this.leafFormList.some((item) => {
          if (item.leafFormCode === '101') {
            // this.leafTypeCd = item.leafTypeCode;
            this.queryItems.leafFormCode = item.leafFormCode;
            console.log(this.queryItems.leafFormCode);
            return true;
          } else {
            return false;
          }
        });
      });
    },

    async handlePagination(pageNumber, pageSize) {
      this.pagination.pageNumber = pageNumber;
      this.pagination.pageSize = pageSize;
      this.onQuery();
      // await this.handleGetLeafTypeList();
    },

    // 获取烟叶类型数据
    async handleGetLeafTypeList() {
      if (!this.leafTypeList?.length) {
        const res = await this.$apis.bpm.executionEnter.queryLeafType({});
        const rows = res?.rows || [];
        this.oldLeafTypeList = _.cloneDeep(rows);
        if (rows && rows.length > 0) {
          rows.unshift({ leafTypeName: '全部', leafTypeCode: '' });
        }
        this.leafTypeList = rows;
        this.leafTypeList.some((item) => {
          if (item.leafTypeName === '烤烟') {
            this.leafTypeCd = item.leafTypeCode;
            this.queryItems.leafTypeCode = item.leafTypeCode;
            this.leafTypeName = '烤烟';
            return true;
          } else {
            return false;
          }
        });
      }
      if (!this.queryItems.leafTypeCode) {
        const { leafTypeCode } = this.leafTypeList[0];
        this.leafTypeCd = leafTypeCode;
        this.queryItems.leafTypeCode = leafTypeCode;
        this.leafTypeName = this.leafTypeList[0].leafTypeName;
      }
      // if (!this.orgCode) {
      //   const orgObj = await this.$apis.bpm.executionEnter.getOrgCode({});
      //   this.orgCode = orgObj?.orgCode; // todo 写死数据
      //   this.orgName = orgObj?.orgName;
      //   // this.orgName = '中国烟草总公司黑龙江省公司';
      //   // this.orgCode = '11000001000000000003';
      //   this.orgCodeList = [this.orgCode];
      // }
      // if (!this.queryItems.busiYear > 0) {
      //   // 获取年度
      //   const data = {
      //     orgCode: this.orgCode, // todo 写死的数据
      //     appCode: this.appCode || 'CM',
      //     yearTypeCode: 'SM_YEAR',
      //   };
      //   const busiYearObj = await this.$apis.bpm.executionEnter.getBuYear(data);
      //   this.queryItems.busiYear = busiYearObj?.yearValue;
      //   this.nowYear = busiYearObj?.yearValue;
      // }
      // this.onQuery();
    },

    // 数据初始化
    async onQuery() {
      this.loading = true;
      // this.leafTypeName = this.beforeLeafTypeName || this.leafTypeName;
      // const { orgCodeList } = this;
      // const applyOrgCode = orgCodeList.length > 0 ? orgCodeList[0] : '';
      // const selectDemandOrg = this.industryUnitList.find(
      //   (item) => item.orgAbbr === this.queryItems.demandOrgCid,
      // );
      this.queryItems.applyOrgCode = this.queryItems.applyOrgCodes.length
        ? this.queryItems.applyOrgCodes.join('')
        : '';
      const data = {
        applyType: 'INVENTORY_CP',
        pageNo: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
        applyOrgCode: this.queryItems.applyOrgCodes.length
          ? this.queryItems.applyOrgCodes.join('')
          : '',
        ...this.queryItems,
      };
      await this._api
        .pageQueryPcTCpApply(data)
        .then((res) => {
          const rows = res.rows || [];
          this.pagination.total = res.totalRows;
          this.oldDataRow = JSON.parse(JSON.stringify(rows));
          this.tableData.rows = rows;
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => {
          this.loading = false;
          this.selectedRows = [];
        });

      // const totalParams = {
      //   leafTypeCode: this.queryItems.leafTypeCode,
      //   busiYear: this.queryItems.busiYear,
      // };
      const totalData = await this.$apis.pcm.buy.packApply.queryTotalData(data);
      // console.log(`汇总数据：${JSON.stringify(totalData)}`);
      this.oldTotalData = JSON.parse(JSON.stringify(totalData));
    },

    // 同意
    onConfirm() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('请选择一条数据再同意');
        return;
      }
      const submitData = this.selectedRows.find((item) => item.applyState === '1');
      // console.log(`--${JSON.stringify(submitData)}`);
      if (submitData) {
        this.$message.error('存在已确认数据，不能重复确认！');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认提交选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].barcodeApplyId);
          }
          // console.log(`ids:${JSON.stringify(idList)}`);
          this.$apis.pcm.buy.packApply
            .confirm({ barcodeApplyIds: idList })
            .then((res) => {
              console.log(JSON.stringify(res));
              if (res.result) {
                this.loading = false;
                this.$message.success('操作成功');
                this.onQuery();
              } else {
                this.loading = false;
                this.$message.error('操作失败');
              }
            })
            .catch((err) => {
              console.log(err);
              this.loading = false;
              // this.$message.error('操作失败');
            });
        },
      });
    },
    // 删除
    onDelete() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('请选择一条数据再同意');
        return;
      }
      const submitData = this.selectedRows.find((item) => item.applyState === '1');
      // console.log(`--${JSON.stringify(submitData)}`);
      if (submitData) {
        this.$message.error('存在已确认数据，不能删除！');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认提交选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].barcodeApplyId);
          }
          this._api
            .deletePcTCpApply({ barcodeApplyIds: idList })
            .then((res) => {
              if (res.result) {
                this.loading = false;
                this.$message.success('操作成功');
                this.onQuery();
              } else {
                this.loading = false;
                this.$message.error('操作失败');
              }
            })
            .catch((err) => {
              console.log(err);
              this.loading = false;
              // this.$message.error('操作失败');
            });
        },
      });
    },

    // 新增
    onAdd() {
      const { tranYear } = this.queryItems;
      const data = {
        type: 'create',
        tranYear,
        enumMap: this.enumMap,
        orgInfo: this.orgInfo,
        oldLeafTypeList: this.oldLeafTypeList,
        orgCodeList: this.orgCodeList,
        orgCode: this.orgCode,
        orgName: this.orgName,
      };
      this.showEdit = true;
      console.log(data);
      this.$nextTick(() => {
        this.$refs.BuyApplyEdit.show(data);
      });
    },

    // 编辑
    onEdit(row) {
      // if (row.applyState === '1') {
      //   this.$message.error('已确认数据不能编辑！');
      //   return;
      // }

      const { tranYear } = this.queryItems;
      const selectedRow = JSON.parse(JSON.stringify(row)); // 深拷贝
      const data = {
        selectedRow,
        tranYear,
        enumMap: this.enumMap,
        oldLeafTypeList: this.oldLeafTypeList,
        applyOrgCodes: this.applyOrgCodes,
        orgCodeList: this.orgCodeList,
        type: 'editor',
      };
      console.log(data);
      this.showEdit = true;
      this.$nextTick(() => {
        this.$refs.BuyApplyEdit.show(data);
      });
    },

    // 重置
    onReset() {
      this.queryItems.leafTypeCode = this.leafTypeCd;
      this.queryItems.packType = this.defaultPackType;
      this.queryItems.storeCode = '';
      this.queryItems.tranYear = new Date().getFullYear();
      this.beforeLeafTypeName = this.handleLeafTypeName(this.leafTypeCd);
      this.$refs.table.pageAction(1);
    },

    // 根据编码翻译对应的名称
    handleLeafTypeName(typeCd) {
      const leafType = this.leafTypeList.find((item) => item.leafTypeCode === typeCd);
      return leafType?.leafTypeName;
    },

    // 选中所有列表
    selectAllEvent({ checked, records }) {
      console.log(`${checked}-选中所有records包含选中的所以对象----${JSON.stringify(records)}`);
      this.selectedRows = records;
    },

    // 选中列表
    selectRowChange({ checked, records }) {
      console.log(
        `${checked}-选择改变时会把records包含选中的所以对象----${JSON.stringify(records)}`,
      );
      this.selectedRows = records;
    },
  },
};
</script>
<style lang="less" scoped>
.year {
  font-size: 18px;
  line-height: 22px;

  color: #18191a;
  font-weight: 700;
  padding-top: 18px;
  padding-right: 50px;
  font-family: '微软雅黑 Bold', '微软雅黑', 'Microsoft YaHei', sans-serif;
}
.paddingTop {
  padding-top: 15px;
}
</style>
